import React from 'react';
import {connect} from "react-redux"

const reviewForm=(props)=>{
    const street_address = props.data.stepperFormData.addressData[0].long_name + props.data.stepperFormData.addressData[1].long_name
    const city = props.data.stepperFormData.addressData[3].long_name
    const country = props.data.stepperFormData.addressData[7].long_name
    const province = props.data.stepperFormData.addressData[6].long_name
     
    const {name, landlord_review}=props.data.stepperFormData.landlord_bio
return(
  <div>
    <h3>Summary</h3>
    <hr />
    <p>Street Address: <span>{street_address}</span></p>
    <p>City: <span>{city}</span></p>
    <p>Province: <span>{province}</span></p>
    <p>Country: <span>{country}</span></p>
    <p>Name: <span>{name}</span></p>
    <p>Bio: <span>{landlord_review}</span></p>
    <div style={{float:"none"}}></div>

    <style jsx>{`
    p {
      width:350px;
      color: #fffffa;
    };

    p span {
      padding:2px;
      color: #FF5941;
      margin-right: 10px;
      float: right
    };

    h3 {
      margin: 0;
      padding:0;
      text-align: center;
      font-family: poppin, san-serf
    };
    hr {
      background-color: #FF5941;
      padding: 1px;
      border: none
    }
    `}</style>
  </div>
)}

const mapStateToProps=(state)=>{
  return{
    data: state.app
  }
}
export default connect(mapStateToProps)(reviewForm)